import React, { useEffect, useState } from "react";

interface UnderConstructionProps {
  name: string;
}

const UnderConstruction: React.FC<UnderConstructionProps> = ({ name }) => {
  const [timeLeft, setTimeLeft] = useState<{
    days: number;
    hours: number;
    minutes: number;
    seconds: number;
  }>({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
  });

  // Extract first letter of each word (for logo)
  const getFirstLetterOfEachWord = (text: string): string => {
    return text
      .split(" ")
      .map((word) => word.charAt(0).toUpperCase())
      .join("");
  };

  useEffect(() => {
    const launchDate = new Date("2025-10-26T00:00:00").getTime();

    const updateCountdown = () => {
      const now = new Date().getTime();
      const diff = launchDate - now;

      if (diff <= 0) {
        setTimeLeft({ days: 0, hours: 0, minutes: 0, seconds: 0 });
        return;
      }

      const days = Math.floor(diff / (1000 * 60 * 60 * 24));
      const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
      const seconds = Math.floor((diff % (1000 * 60)) / 1000);

      setTimeLeft({ days, hours, minutes, seconds });
    };

    updateCountdown();
    const timer = setInterval(updateCountdown, 1000);
    return () => clearInterval(timer);
  }, []);

  return (
    <div style={styles.body}>
      <div style={styles.logo}>{getFirstLetterOfEachWord(name)}</div>
      <h1 style={styles.h1}>Website Under Construction</h1>
      <p style={styles.p}>Our new website is coming soon. Stay tuned!</p>

      <div style={styles.countdown}>
        <CountdownBox label="Days" value={timeLeft.days} />
        <CountdownBox label="Hours" value={timeLeft.hours} />
        <CountdownBox label="Minutes" value={timeLeft.minutes} />
        <CountdownBox label="Seconds" value={timeLeft.seconds} />
      </div>

      <footer style={styles.footer}>
        &copy; {new Date().getFullYear()} {name}. All rights reserved.
      </footer>
    </div>
  );
};

const CountdownBox: React.FC<{ label: string; value: number }> = ({ label, value }) => (
  <div style={styles.box}>
    <span style={styles.value}>{String(value).padStart(2, "0")}</span>
    {label}
  </div>
);

const styles: Record<string, React.CSSProperties> = {
  body: {
    fontFamily: "'Poppins', Arial, sans-serif",
    background: "linear-gradient(135deg, #004aad, #0c2340)",
    color: "#fff",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column",
    height: "100vh",
    textAlign: "center",
    overflow: "hidden",
  },
  logo: {
    width: "90px",
    height: "90px",
    borderRadius: "50%",
    background: "#fff",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    fontWeight: "bold",
    color: "#0c2340",
    fontSize: "1.5rem",
    marginBottom: "1rem",
    boxShadow: "0 4px 20px rgba(0, 0, 0, 0.3)",
  },
  h1: {
    fontSize: "2.5rem",
    letterSpacing: "1px",
    marginBottom: "0.5rem",
  },
  p: {
    fontSize: "1.1rem",
    opacity: 0.9,
    marginBottom: "1.5rem",
  },
  countdown: {
    display: "flex",
    gap: "20px",
    justifyContent: "center",
    marginBottom: "1.5rem",
    flexWrap: "wrap",
  },
  box: {
    background: "rgba(255, 255, 255, 0.1)",
    padding: "10px 15px",
    borderRadius: "8px",
    minWidth: "70px",
  },
  value: {
    display: "block",
    fontSize: "1.3rem",
    fontWeight: "bold",
  },
  footer: {
    position: "absolute",
    bottom: "15px",
    fontSize: "0.9rem",
    opacity: 0.8,
  },
};

export default UnderConstruction;
