// Framework.tsx
import React from "react";
import { Reveal } from "../reveal/Reveal";
import "./FrameworkIntro.css";
import { getIcon } from "../../content/get-icon";

const Framework: React.FC<{ content: any }> = ({ content }) => {
  if (!content) return null;
  
  const {
    sectionId,
    ariaLabel,
    heading,
    subheading,
    body,
    fiveTHeading,
    eightFactorsHeading,
    fiveT,
    eightFactors,
  } = content;

  return (
    <section className="opop-framework-section" id={sectionId} aria-label={ariaLabel}>
      <div className="opop-framework-container">
        {/* Section Header */}
        <Reveal direction="down" delay={100}>
          <h2 className="opop-framework-heading">{heading}</h2>
        </Reveal>
        <Reveal direction="up" delay={200}>
          <h3 className="opop-framework-subline">{subheading}</h3>
        </Reveal>
        <Reveal direction="up" delay={300}>
          <p className="opop-framework-body">{body}</p>
        </Reveal>

        {/* 5T Timeline */}
        <Reveal direction="left" delay={400}>
          <div className="opop-timeline-heading">{fiveTHeading}</div>
        </Reveal>
        <div className="opop-timeline-container">
          {fiveT.map((step:any, i:number) => {
            const Icon = getIcon(step.icon);
            return (
              <Reveal key={i} direction="up" delay={500 + i * 100}>
                <div className="opop-timeline-step">
                  <div className="opop-timeline-top">
                    <span className="opop-timeline-id">{step.id}</span>
                    <span className="opop-timeline-title">{step.title}</span>
                  </div>
                  <div className="opop-timeline-circle">
                    {Icon && <Icon aria-hidden="true" />}
                  </div>
                  <div className="opop-timeline-bottom">{step.desc}</div>
                </div>
              </Reveal>
            );
          })}
        </div>

        {/* Divider */}
        <Reveal direction="scale" delay={1000}>
          <div className="opop-framework-divider"></div>
        </Reveal>

        {/* 8 Factors */}
        <Reveal direction="right" delay={1100}>
          <div className="opop-timeline-heading">{eightFactorsHeading}</div>
        </Reveal>
        <div className="opop-factors-grid">
          {eightFactors.map((factor : any, i : number ) => {
            const Icon = getIcon(factor.icon);
            return (
              <Reveal key={i} direction="up" delay={1200 + i * 100}>
                <div className="opop-factor-card">
                  <div className="opop-factor-icon">
                    {Icon && <Icon aria-hidden="true" />}
                  </div>
                  <h4 className="opop-factor-title">{factor.title}</h4>
                </div>
              </Reveal>
            );
          })}
        </div>
      </div>
    </section>
  );
};

export default Framework;
