// About.tsx
import React from "react";
import "./about.css";

const About: React.FC<{ content: any }> = ({ content }) => {
  if (!content) return null;

  return (
    <section className="intro-section" id={content.sectionId}>
      <div className="intro-container">
        {/* Left Text */}
        <div className="intro-text">
          <h2 className="intro-heading">{content.heading}</h2>
          <h3 className="intro-subline">{content.subline}</h3>
          
          {content.paragraphs.map((para : any , index : number ) => (
            <p className="intro-body" key={index}>{para}</p>
          ))}

          <a href={content.link.href} className="intro-link">
            {content.link.label}
          </a>
        </div>

        {/* Right Image */}
        <div className="intro-image">
          <img
            src={content.image.src}
            alt={content.image.alt}
          />
        </div>
      </div>
    </section>
  );
};

export default About;
