// NominationCTA.tsx
import React from "react";
import "./NominationCTA.css";
import { Link } from "gatsby";
import { getIcon } from "../../content/get-icon";

const NominationCTA: React.FC<{ content: any }> = ({ content }) => {
  if (!content) return null;
  
  const { heading, description, cta } = content;
  const CtaIcon = getIcon(cta.icon);

  return (
    <section className="cta-section">
      <div className="footer-grid-bg"></div>
      <div className="cta-container">
        <h2 className="cta-heading">{heading}</h2>
        <p className="cta-text">{description}</p>

        <div className="thv-hero__cta-row cta-buttons">
          <Link to={cta.href} className="thv-btn thv-btn--gold">
            {cta.label}
            {CtaIcon && <CtaIcon aria-hidden="true" />}
          </Link>
        </div>
      </div>
    </section>
  );
};

export default NominationCTA;
