"use client";
import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Autoplay, Pagination } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "./Honorees.css";

import { generateSlug } from "./slugUtils";
import { navigate } from "gatsby";

const Honorees: React.FC<{ content: any }> = ({ content }) => {
  if (!content) return null;
  
  const { sectionId, ariaLabel, heading, subline, cta, honorees } = content;

  // Hide if less than 3 honorees
  if (!honorees || honorees.length < 3) {
    return null;
  }

  return (
    <section className="honorees-section" id={sectionId} aria-label={ariaLabel}>
      <div className="honorees-container">
        {/* Section Headings */}
        <h2 className="honorees-heading">{heading}</h2>
        <h3 className="honorees-subline">{subline}</h3>

        {/* Swiper Slider */}
        <div className="honorees-slider-wrapper">
          <Swiper
            modules={[Navigation, Autoplay, Pagination]}
            spaceBetween={24}
            slidesPerView={3}
            navigation={{
              nextEl: ".honorees-button-next",
              prevEl: ".honorees-button-prev",
            }}
            pagination={{ clickable: true }}
            autoplay={{ delay: 3500, disableOnInteraction: false }}
            loop
            breakpoints={{
              0: { slidesPerView: 1 },
              640: { slidesPerView: 2 },
              1024: { slidesPerView: 3 },
            }}
            className="honorees-swiper"
          >
            {honorees.slice(0, 6).map((honoree : any , i : number ) => (
              <SwiperSlide key={i}>
                <div
                  className="honoree-card cursor-pointer hover:transform hover:scale-105 transition-all duration-300"
                  onClick={() =>
                    navigate(`/honorees/${generateSlug(honoree.name)}`)
                  }
                >
                  <div className="honoree-img-wrap relative overflow-hidden rounded-lg shadow-md">
                    <img
                      src={honoree.image}
                      alt={honoree.name}
                      className="w-full h-64 object-cover"
                    />
                    <div className="honoree-overlay absolute inset-0 bg-black/50 text-white flex flex-col justify-end p-4 opacity-0 hover:opacity-100 transition-all duration-300">
                      <h4 className="honoree-name text-lg font-semibold">
                        {honoree.name}
                      </h4>
                      <span className="honoree-category text-sm italic">
                        {honoree.category}
                      </span>
                      {honoree.fieldOfEstablishment && (
                        <p className="honoree-field text-xs mt-1 opacity-90">
                          {honoree.fieldOfEstablishment}
                        </p>
                      )}
                      <div className="mt-2">
                        <span className="text-yellow-400 text-xs font-semibold">
                          View Profile â†’
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              </SwiperSlide>
            ))}
          </Swiper>

          {/* Navigation Buttons */}
          <div className="honorees-button-prev">â€¹</div>
          <div className="honorees-button-next">â€º</div>
        </div>

        {/* CTA Button */}
        <div className="honorees-cta mt-8 text-center">
          <a
            href={cta.href}
            className="honorees-btn inline-block bg-[#d1a12a] text-white px-6 py-2 rounded-full shadow hover:bg-[#b68d24] transition-all duration-300"
          >
            {cta.label}
          </a>
        </div>
      </div>
    </section>
  );
};

export default Honorees;
