// Hero.tsx
import React, { useEffect, useMemo, useState } from "react";
import { Link } from "gatsby";
import { Reveal } from "../reveal/Reveal";

import "./hero.css";
import "../reveal/reveal.css";
import { getIcon } from "../../content/get-icon";

const Hero: React.FC<{ content: any }> = ({ content }) => {
  const [active, setActive] = useState(0);
  const [isVisible, setIsVisible] = useState(false);

  // console.log("HeroSection content:", content);

  if (!content) return null;

  const { slides, highlights, meta } = content;
  const currentSlide = slides[active];


  const TempIcon = () => <>!</>
  const CtaIcon : any = getIcon(content.ctaIcon) ?? TempIcon
  console.log(content.ctaIcon)
  console.log(content)

  const prefersReducedMotion = useMemo(() => {
    if (typeof window === "undefined") return true;
    return window.matchMedia("(prefers-reduced-motion: reduce)").matches;
  }, []);

  useEffect(() => {
    setIsVisible(true);

    if (prefersReducedMotion || slides.length <= 1) return;

    const interval = setInterval(() => {
      setActive((current) => (current + 1) % slides.length);
    }, 8000);

    return () => clearInterval(interval);
  }, [prefersReducedMotion]);

  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      if (event.key === 'ArrowUp') {
        event.preventDefault();
        setActive((prev) => (prev - 1 + slides.length) % slides.length);
      } else if (event.key === 'ArrowDown') {
        event.preventDefault();
        setActive((prev) => (prev + 1) % slides.length);
      } else if (event.key >= '1' && event.key <= '9') {
        const slideIndex = parseInt(event.key) - 1;
        if (slideIndex < slides.length) {
          setActive(slideIndex);
        }
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [slides.length]);

  return (
    <section
      className="thv-hero"
      aria-label={meta.sectionLabel}
      style={{ opacity: isVisible ? 1 : 0, transition: 'opacity 0.8s ease-in-out' }}
    >
      <img
        src={currentSlide.background}
        alt={currentSlide.alt}
        className="thv-hero__bg"
        loading="eager"
        key={currentSlide.id}
      />
      <div className="thv-hero__overlay" />

      <div className="thv-hero__inner">
        <div className="thv-hero__left">
          <Reveal direction="down" delay={200}>
            <p className="thv-hero__eyebrow">{meta.eyebrow}</p>
          </Reveal>

          <Reveal direction="up" delay={300}>
            <h1 className="thv-hero__title">
              <span className="thv-hero__title-main">{currentSlide.title}</span>
              <span className="thv-hero__title-sub">{currentSlide.subtitle}</span>
            </h1>
          </Reveal>

          <Reveal direction="up" delay={400}>
            <p className="thv-hero__desc">{currentSlide.description}</p>
          </Reveal>

          <Reveal direction="up" delay={500}>
            <div className="thv-hero__cta-row">
              <Link
                to={currentSlide.ctaHref}
                className="thv-btn thv-btn--gold"
                aria-label={`Primary action: ${currentSlide.ctaLabel}`}
              >
                {currentSlide.ctaLabel}
                {CtaIcon && <CtaIcon aria-hidden="true" />}
              </Link>

              {currentSlide.secondaryHref && currentSlide.secondaryLabel && (
                <Link
                  to={currentSlide.secondaryHref}
                  className="thv-btn thv-btn--ghost"
                  aria-label={`Secondary action: ${currentSlide.secondaryLabel}`}
                >
                  {currentSlide.secondaryLabel}
                </Link>
              )}
            </div>
          </Reveal>

          <Reveal direction="up" delay={600}>
            <ul className="thv-hero__highlights" aria-label="Key Features">
              {highlights.map((highlight : any , index : number ) => {
                const Icon = getIcon(highlight.icon);
                return (
                  <Reveal key={index} direction="left" delay={700 + index * 100}>
                    <li className="thv-hero__highlight">
                      <span className="thv-ico" aria-hidden="true">
                        {Icon && <Icon />}
                      </span>
                      <div>
                        <div className="thv-hl-title">{highlight.title}</div>
                        <div className="thv-hl-detail">{highlight.detail}</div>
                      </div>
                    </li>
                  </Reveal>
                );
              })}
            </ul>
          </Reveal>
        </div>
      </div>
    </section>
  );
};

export default Hero;
