"use client";

import React, { useEffect } from "react";
import "./FeaturedVideoSection.css";

type Props = {
  content: {
    heading?: string;
    subheading?: string;
    videoUrl?: string;
  };
  hide?: boolean;
};

function isTikTok(url?: string) {
  return !!url && url.includes("tiktok.com");
}

function getYouTubeEmbed(url?: string) {
  if (!url) return "";

  // already embed url
  if (url.includes("youtube.com/embed")) return url;

  // watch?v=xxxx
  const watchMatch = url.match(/v=([^&]+)/);
  if (watchMatch) {
    return `https://www.youtube.com/embed/${watchMatch[1]}`;
  }

  // youtu.be/xxxx
  const shortMatch = url.match(/youtu\.be\/([^?]+)/);
  if (shortMatch) {
    return `https://www.youtube.com/embed/${shortMatch[1]}`;
  }

  return url;
}

const FeaturedVideoSection = ({ content, hide }: Props) => {
  if (!content || hide) return null;

  // Ensure TikTok embed script loads
  useEffect(() => {
    if (content.videoUrl && isTikTok(content.videoUrl)) {
      const existingScript = document.querySelector(
        "script[src='https://www.tiktok.com/embed.js']"
      );
      if (!existingScript) {
        const script = document.createElement("script");
        script.src = "https://www.tiktok.com/embed.js";
        script.async = true;
        document.body.appendChild(script);
      }
    }
  }, [content.videoUrl]);

  return (
    <section className="featured-video-section" id="featured-video">
      <div className="featured-video-container">
        {content.heading && (
          <h2 className="featured-video-heading">{content.heading}</h2>
        )}

        {content.subheading && (
          <h3 className="featured-video-subheading">
            {content.subheading}
          </h3>
        )}

        {/* ðŸŽ¬ VIDEO RENDER */}
        {content.videoUrl && !isTikTok(content.videoUrl) && (
          <div className="video-wrapper desktop-video">
            <iframe
              width="560"
              height="315"
              src={getYouTubeEmbed(content.videoUrl)}
              title="Featured Video"
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              referrerPolicy="strict-origin-when-cross-origin"
              allowFullScreen
            />
          </div>
        )}

        {content.videoUrl && isTikTok(content.videoUrl) && (
          <div className="video-wrapper mobile-video">
            <blockquote
              className="tiktok-embed"
              cite={content.videoUrl}
            >
              <section>
                <a
                  target="_blank"
                  href={content.videoUrl}
                  rel="noreferrer"
                >
                  Watch on TikTok
                </a>
              </section>
            </blockquote>
          </div>
        )}
      </div>
    </section>
  );
};

export default FeaturedVideoSection;
