import * as React from "react";

import HeroSection from "../componets/hero/hero";
import About from "../componets/about/about";
import FrameworkIntro from "../componets/FrameworkIntro/FrameworkIntro";
import Honorees from "../componets/Honorees/Honorees";
import NominationCTA from "../componets/NominationCTA/NominationCTA";
import SponsorsSection from "../componets/SponsorsSection/SponsorsSection";
import FeaturedVideoSection from "../componets/FeaturedVideoSection/FeaturedVideoSection";
import UnderConstruction from "../componets/under-construction/UnderConstruction";
import Header from "../componets/header/Header";
import Footer from "../componets/footer/Footer";
import { useStaticConfig } from "../hooks/useStaticConfig";
import { getContent, getContentKey } from "../content/content";
import { useLanguage } from "../context/LanguageContext";
import { useStorage } from "../hooks/useStorage";
import { contentDynamic } from "../content/settings";

const IndexPage = () => {
  const storage = useStorage();
  console.log(storage)

  const [isBypassed, setIsBypassed] = React.useState(false);
  const [isAdminPreview, setIsAdminPreview] = React.useState(false);
  const [adminData, setAdminData] = React.useState<any>(null);
  const [ remoteContent, setRemoteContent ] = React.useState<any>( undefined)
  const [ defaultContent, setDefaultContent ] = React.useState<any>( contentDynamic )


  const { UNDER_CONSTRUCTION, HIDE_LANDING_VIDEO_SECTION, CONTENT } =
    useStaticConfig();

    console.log(CONTENT)

    React.useEffect(() => {
    console.log("AFTER EFFECT:", CONTENT);
    if(CONTENT){ 
      setRemoteContent(CONTENT)
      
      console.log('will override now!')
    }
    else console.log('we will not override!')


  }, [CONTENT]);
    

  React.useEffect(() => {
    if (typeof window === "undefined") return;

    const isPreview = typeof window !== "undefined" && window.location.host.includes("preview.");
    if( isPreview ) return;

    const params = new URLSearchParams(window.location.search);
    if (params.get("user") === "admin") {
      setIsBypassed(true);
    }

    if (storage) {
      try {
        const raw = storage.getItem("admin");

        
        
        if (raw) {
          
          setAdminData(JSON.parse(raw));
          setIsAdminPreview(true);
        }
      } catch {
        // ignore bad data
      }
    }
  }, [storage]);

  if (UNDER_CONSTRUCTION && !isBypassed) {
    return <UnderConstruction name="Tinh Hoa Viá»‡t" />;
  }
let content: any =
  remoteContent ??
  (isAdminPreview ? adminData : defaultContent);

const normalized = content?.CONTENT ? content.CONTENT : content;

console.log("FINAL content selected:", content);
console.log("FINAL normalized:", normalized);

return (
  <LandingPage
    settings={{ hideVideo: HIDE_LANDING_VIDEO_SECTION }}
    isAdminPreview={isAdminPreview}
    setIsAdminPreview={setIsAdminPreview}
    adminData={{
      exists: !!adminData,
      content: normalized,
    }}
  />
);

};

export default IndexPage;

const LandingPage = ({
  settings,
  isAdminPreview,
  setIsAdminPreview,
  adminData,
}: any) => {
  const { lang } = useLanguage();

  const adminDataExists = adminData?.exists

  const content = adminData.content
    ? adminData.content[getContentKey(lang)]
    : getContent(lang);

  const dynamicContent = content?.pages?.globals?.section;
  if (!dynamicContent) {
    return <div style={{ padding: 24 }}>Missing globals.section</div>;
  }
  const headerContent = dynamicContent.headerContent;
  const footerContent = dynamicContent.footerContent;
  const landing = content.pages.landing.section;

  
  return (
    <main style={{ background: "#1A202C", minHeight: "100vh" }}>
      
      {adminDataExists && (
        <div style={{ height: 50 }}>
          <div
            style={{
              height: 50,
              width: "100%",
              backgroundColor: "#7f1d1d",
              position: "fixed",
              top: 0,
              left: 0,
              zIndex: 1000,
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              padding: "0 16px",
              color: "#fff",
              fontSize: 14,
            }}
          >
            <div
              style={{ cursor: "pointer", fontWeight: 500 }}
              onClick={() => {
                if (typeof window !== "undefined") {
                  window.location.href = "/cms/editor";
                }
              }}
            >
              â† Back to editor
            </div>

            <div style={{ fontWeight: 600 }}>
              {isAdminPreview ? "Preview Mode: ON" : "Preview Mode: OFF"}
            </div>

            <button
              onClick={() => setIsAdminPreview((v: boolean) => !v)}
              style={{
                padding: "6px 14px",
                borderRadius: 4,
                border: "none",
                background: isAdminPreview ? "#dc2626" : "#16a34a",
                color: "#fff",
                fontWeight: 600,
                cursor: "pointer",
              }}
            >
              {isAdminPreview ? "Disable Preview" : "Enable Preview"}
            </button>
 
          </div>
        </div>
      )}

      <Header content={headerContent} />

      <HeroSection content={landing?.heroContent} />
      <SponsorsSection content={landing?.sponsorsContent} />
      <About content={landing?.aboutContent} />
      <FrameworkIntro content={landing?.frameworkContent} />

      <FeaturedVideoSection
        content={landing?.featuredVideoContent}
        hide={settings?.hideVideo}
      />

      <Honorees content={landing?.honoreesContent} />
      <NominationCTA content={landing?.nominationCtaContent} />
      <Footer content={footerContent} />
    </main>
  );
};
